
import { defineComponent } from "vue";

const Component = defineComponent({
  name: "NotFound",
  data() {
    return {
      time: 5
    };
  },
  created() {
    setInterval(() => {
      if (this.time > 0) this.time--;
      else this.$router.push("/");
    }, 1000);
  }
})

export default Component;
